*{
    margin: 0;
    padding: 0;
}
.body{
    font-family: sans-serif;
}
.app{
    background-image: url('./asseset/cold.jpg');
    background-size: cover;
    background-position: bottom;
    transition: 0.4 ease;
}
.app.warm{
    background-image: url('./asseset/warm.jpg');
}
main{
    min-height: 100vh;
    background-image: linear-gradient(to bottom,rgba(0,0,0,0.2),rgba(0,0,0,0.50));
    padding: 25px;
}
.searchBox{
    width: 100%;
    margin: 0 0 75px;
}
.searchBox .searchBar{
    display: block;
    width: 100%;
    padding: 13px;
    outline: none;
    background: none;
    border-color: rgba(255, 255, 255, 0.5);
    border-radius: 15px 15px 15px 15px;
    color: #313131;
    font-size: 20px;
    transition: 0.4 ease;
}
.searchBox .searchBar:focus{
background-color: rgba(255, 255, 255, 0.75);
}
.locationBox .location{
    font-size: 30px;
    font-weight: 500;
    color: seashell;
    text-align: center;
    font-style: oblique;
}
.locationBox .date{
    font-size: 25px;
    font-weight: 400;
    color: seashell;
    text-align: center;
    font-style: italic;
}
.weatherBox{
    text-align: center;
}
.weatherBox .temp{
    position: relative;
    display: inline-block;
    margin: 20px auto;
    background-color: rgba(233, 229, 229, 0.5);
    border-radius: 16px;
    
    padding: 15px 25px;

    color:#fff;
    font-size: 60px;
    font-weight: 400;

    text-shadow: rgba(50,50,70,0.5);
    text-align: center;

}
.weatherBox .weather{
    color: #fff;
    font-size: 30px;
    font-weight: 500;

}